
import logo from '../../logo.svg';
import './App.css';

function App() {
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <h1>Welcome To Seinfeld - An app about nothing.</h1>
      </header>
    </div>
  );
}

export default App;
